import { connect } from 'react-redux';
import { PropTypes } from 'react';

import Webphone from 'ringcentral-integration/modules/Webphone';
import Locale from 'ringcentral-integration/modules/Locale';

import ActiveCallPanel from '../../components/ActiveCallPanel';

function mapToProps(_, {
  webphone,
  locale,
}) {
  const currentSession = webphone.currentSession;
  return {
    currentLocale: locale.currentLocale,
    sessionId: currentSession && currentSession.id,
    active: !!currentSession,
    minimized: webphone.minimized,
    userName: 'test',
    phoneNumber: '12345678',
  };
}

function mapToFunctions(_, {
  webphone,
}) {
  return {
    hangup: () => {
      webphone.hangup(webphone.activeSession);
    },
    toggleMinimized: webphone.toggleMinimized,
  };
}

const ActiveCallPage = connect(
  mapToProps,
  mapToFunctions,
)(ActiveCallPanel);

ActiveCallPage.propTypes = {
  webphone: PropTypes.instanceOf(Webphone).isRequired,
  locale: PropTypes.instanceOf(Locale).isRequired,
};

export default ActiveCallPage;

